import axios from 'axios'
import { 
    PAGE_LIST_REQUEST, 
    PAGE_LIST_SUCCESS, 
    PAGE_LIST_FAIL, 
    PAGE_DETAILS_REQUEST, 
    PAGE_DETAILS_SUCCESS, 
    PAGE_DETAILS_FAIL, 
    PAGE_CREATE_SUCCESS, 
    PAGE_CREATE_REQUEST, 
    PAGE_CREATE_FAIL, 
    PAGE_DELETE_SUCCESS, 
    PAGE_DELETE_FAIL, 
    PAGE_DELETE_REQUEST, 
    PAGE_UPDATE_REQUEST,
    PAGE_UPDATE_SUCCESS,
    PAGE_UPDATE_FAIL
} from '../constants/pageConstants'


export const listPages = (userName) => async (dispatch, getState) => {
    try {
        dispatch({ type: PAGE_LIST_REQUEST })

        const { userLogin: { userInfo } } = getState()

        let dataFromBackend

        if (userInfo && userInfo.token) {
            const config = {
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${userInfo.token}`
                }
            }

            dataFromBackend = await axios.get(`/${userName}/api/pages/all`, config)
        } else {
            dataFromBackend = await axios.get(`/${userName}/api/pages`)
        }

        const { data } = dataFromBackend

        dispatch({
            type: PAGE_LIST_SUCCESS,
            payload: data
        })
    } catch (err) {
        dispatch({
            type: PAGE_LIST_FAIL,
            payload: err.response && err.response.data.message ? err.response.data.message : err.message
        })
    }
}

