import React, { useState } from 'react'
import { Card, Button, Form } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import { useDispatch } from 'react-redux'
import { deletePage } from '../actions/pageActions'

const getFileName = (fileLoc) => {
    fileLoc = fileLoc.split("").reverse()
    let i;
    for (i = 0; i < fileLoc.length; i++) {
        const char = fileLoc[i];
        if (char == '/') {
            break;
        }
    }
    fileLoc.length = i;
    fileLoc.reverse();
    fileLoc.join("")
    return fileLoc;
}

const Page = ({ page, userName, history }) => {
    const dispatch = useDispatch()

    const deleteHandler = () => {
        dispatch(deletePage(userName, page))
        history.go(0)
    }

    const editHandler = () => {
        history.push(`/${userName}/edit-page/${page._id}`)
    }
    return (
        <LinkContainer to={`/${userName}/page/${page._id}`} style={{
            display: 'grid',
            gridTemplateRows: `
                15% 
                auto 
                auto
            `,
            gridTemplateAreas:`
                'name name name'
                'text text text'
                'files files files'
            `,
            columnGap: '10px',
            rowGap: '10px',
            cursor: 'pointer'
        }}>
            <Card className='my-3 p-3 rounded page'>
                <Card.Title style={{gridArea: 'name'}} className='m-0'>{page.name}</Card.Title>
                {page.text ?
                    <Card.Text style={{gridArea: 'text'}}  className='m-0'>
                        {page.text}
                    </Card.Text>
                    : page.text = ''
                }
                {page.files.map(file => (
                        <Card.Text key={file} style={{gridArea: 'files'}}  className='m-0'>
                            <a href={file} download>
                                <Button variant="outline-dark">
                                    {getFileName(file)}
                                </Button>
                            </a>
                        </Card.Text>
                    ))
                }
            </Card>
        </LinkContainer>
    )
}

export default Page
export {
    getFileName
}
