import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { Card, Form, Button, Modal, OverlayTrigger, Tooltip } from 'react-bootstrap'
import { Switch } from 'evergreen-ui'
import { LinkContainer } from 'react-router-bootstrap'
import Message from '../components/Message'
import { createNewPage } from '../actions/pageActions'

const NewPageScreen = ({ match, history }) => {    
    const [name, setName] = useState('' )
    const [text, setText] = useState('')
    const [files, setFiles] = useState([])
    const [publicViewing, setPublicViewing] = useState(false)
    const [isOneFactorAuthReqForViewing, setIsOneFactorAuthReqForViewing] = useState(false)
    const [publicEditing, setPublicEditing] = useState(false)
    const [publicEditingModalShown, setPublicEditingModalShown] = useState(false)
    const [publicViewingModalShown, setPublicViewingModalShown] = useState(false)
    const [message, setMessage] = useState(null)

    const dispatch = useDispatch()

    const submitHandler = (e) => {
        if (!name) {
            setMessage('Name is required')
        } else {
            dispatch(createNewPage(match.params.userName, {
                name,
                text,
                files,
                publicViewing,
                isOneFactorAuthReqForViewing,
                publicEditing
            }))
            history.push(`/${match.params.userName}`)
            history.go(0)
        }
    }

    const PublicEditingModalCancel = () => {
        setPublicEditingModalShown(false)
        setPublicEditing(!publicEditing)
    }

    const PublicViewingModalCancel = () => {
        setPublicViewingModalShown(false)
        setPublicViewing(!publicViewing)
    }

    const toggleIsEditingPublic = () => {
        setPublicEditing(!publicEditing)
        if (!publicEditing) {
            setPublicEditingModalShown(true)
        }
    }

    const toggleIsViewingPublic = () => {
        setPublicViewing(!publicViewing)
        if (!publicViewing) {
            setPublicViewingModalShown(true)
        }
    }


    const PublicEditingModal = () => (
        <>
            <Modal show={publicEditingModalShown} onHide={PublicEditingModalCancel} centered>
                <Modal.Header>
                    <Modal.Title>Public Editing</Modal.Title>
                </Modal.Header>

                <Modal.Body className='p-4'>
                    Please note that one factor authentication is required. 
                    In order to edit this page from an unauthorized computer, 
                    you will have to enter a code that will be sent to you via SMS or email
                </Modal.Body>

                <Modal.Footer>
                    <Button variant="light" onClick={PublicEditingModalCancel}>
                        Cancel
                    </Button>
                    <Button variant="dark" onClick={() => setPublicEditingModalShown(false)}>
                        Save Changes
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    )

    const PublicViewingModal = () => (
        <>
            <Modal show={publicViewingModalShown} onHide={PublicViewingModalCancel} centered>
                <Modal.Header>
                    <Modal.Title>Public Viewing</Modal.Title>
                </Modal.Header>

                <Modal.Body className='p-4'>
                    By setting viewing public, you can decide whether or not one factor authentication is required. 
                    <br/> 
                    What does that mean? 
                    When set to true, in order to view the note from an unauthorized computer you will have to enter a code that will be sent to you via SMS or email. 
                </Modal.Body>

                <Modal.Footer>
                    <Button variant="light" onClick={PublicViewingModalCancel}>
                        Cancel
                    </Button>
                    <Button variant="dark" onClick={() => setPublicViewingModalShown(false)}>
                        Okay
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    )

    return (
        <>
            {message && <Message variant='danger'>{message}</Message>}
            <LinkContainer to={`/${match.params.userName}`}>
                <Button variant='light' className='p-0'>
                    <Card className="p-3">
                        <i className='fas fa-arrow-left'></i>
                    </Card>
                </Button>
            </LinkContainer>
            <Card className="p-3">
                <Card.Title>
                    Create a new page:
                </Card.Title>
                <Form>
                    <Form.Group className="mb-3">
                        <Form.Label>page name</Form.Label>
                        <Form.Control 
                            type="text" 
                            placeholder="Enter name" 
                            value={name} 
                            onChange={e => setName(e.target.value)} 
                            required
                        />
                    </Form.Group>
                
                    <Form.Group className="mb-3">
                        <Form.Label>page text</Form.Label>
                        <Form.Control 
                            type="text" 
                            placeholder="Enter text" 
                            value={text}
                            onChange={e => setText(e.target.value)}
                        />
                    </Form.Group>

                    <Form.Group className="mb-3">
                        <Form.Control 
                            type="file" 
                            multiple 
                            value={files} 
                            onChange={e => setFiles(e.target.value)}
                        />
                    </Form.Group>

                    <Form.Group className="mb-3">
                        <Form.Label>Is viewing Public?</Form.Label>
                        <Switch checked={publicViewing} onChange={toggleIsViewingPublic} />
                    </Form.Group>

                    {publicViewing &&
                        <Form.Group className="mb-3">
                            <Form.Label>
                                Is one factor authentication required for viewing this page?
                                <OverlayTrigger overlay={
                                    <Tooltip>
                                        In order to view the note from an unauthorized computer you will have to enter a code that will be sent to you via SMS or email.
                                    </Tooltip>
                                }>
                                    <i class="far fa-question-circle mx-1"></i>
                                </OverlayTrigger>
                            </Form.Label>
                            <Switch checked={isOneFactorAuthReqForViewing} onChange={() => setIsOneFactorAuthReqForViewing(!isOneFactorAuthReqForViewing)} />
                        </Form.Group>
                    }

                    <Form.Group className="mb-3">
                        <Form.Label>Is Editing Public?</Form.Label>
                        <Switch checked={publicEditing} onChange={toggleIsEditingPublic} />
                    </Form.Group>

                    <LinkContainer to={`/${match.params.userName}`} disabled={!name || name.length < 1}>
                        <Button variant="outline-dark" type="submit" onClick={submitHandler}>
                            create!
                        </Button>
                    </LinkContainer>
                </Form>
            </Card>
            <PublicEditingModal />
            <PublicViewingModal />
        </>
    )
}

export default NewPageScreen
